.main-carousel {
  width: var(--main-carousel-width);
  height: var(--main-carousel-height);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.main-carousel__slides-container,
.main-carousel__swicth {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-carousel__slides-container {
  position: relative;
  height: inherit;
}

.main-carousel__slide {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  opacity: 0;
  filter: blur(1000px);
  pointer-events: none;
  transition:
    opacity 0.5s ease-out,
    filter 1s ease-out;
}

.main-carousel__slide--visible {
  opacity: 1;
  filter: blur(0);
  pointer-events: auto;
  transition: opacity 0.5s ease-out;
}

.main-carousel__slide-info {
  display: flex;
  flex-direction: column;
  position: absolute;
  padding: 10px;
  left: 0;
  right: 0;
  bottom: 0;
  gap: 10px;
  /*  width: var(--main-carousel-slide-info-width);*/
  background-color: rgba(0, 0, 0, 0.6);
}

.main-carousel__collection-description {
  margin: 0;
  color: #fff;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
}

.main-carousel__slide-catalog-link {
  align-self: center;
}

.main-carousel__swicth {
  display: flex;
  gap: 29px;
  width: 80%;
  align-self: center;
}

.main-carousel__swicth-button {
  flex-grow: 1;
}

.main-carousel__radio {
  display: none;
}

.main-carousel__label {
  display: block;
  height: 20px;
  width: 100%;
  cursor: pointer;
  border-radius: 10px;
  background-color: silver;
  transition: background 0.2s linear;
}

.main-carousel__label:hover {
  background-color: #fff;
}

.main-carousel__radio:checked + .main-carousel__label {
  background-color: var(--main-yellow);
}

